<template>
	<div class="inner">
		<h1>userHome</h1>
		<router-link :to="{ name: 'setting'}">to setting</router-link>
		<span class="reg-btn reg-btn-red" @click="quitLogin">退出登录</span>
		<div class="ll">
			a
		</div>
	</div>
</template>

<script>
	export default {
		created() {
			document.body.className = 'gray-bg';
		},
		methods: {
			quitLogin () {
				console.log('quit');
				this.$router.push({
	              path: '/'
	            });
			    localStorage.removeItem('userinfo');
			    this.$store.dispatch('UPDATE_USERINFO', '');
			}
		},
		mounted () {
			this.$store.commit('UPDATE_LOADING', false);
		}
	}
</script>

<style lang="scss">
	.ll{
		display: flex;
		align-items: center;
	}
</style>


